.about {
  margin-bottom: 117px;

  @media (max-width: $tablet-width) {
    margin-bottom: 93px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 62px;
  }
}

.about__description-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  box-sizing: content-box;
  width: 1160px;
  margin: 0 auto;

  @media (max-width: $tablet-width) {
    width: 678px;
  }

  @media (max-width: $mobile-width) {
    flex-direction: column;
    width: auto;
  }
}

.about__description-text-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: flex-start;
  color: $color-default-black;
  padding: 58px 0;
  margin-right: 30px;
  width: 512px;

  @media (max-width: $tablet-width) {
    width: 324px;
    padding: 0;
  }

  @media (max-width: $mobile-width) {
    width: 290px;
    padding: 0 15px;
    margin: 0 auto;
    margin-bottom: 33px;
    box-sizing: content-box;
  }

  @media (max-width: $mobile-width) and (min-width: $adaptive-mobile) {
    width: auto;
    margin: 0;
    margin-bottom: 33px;
    text-align: center;
  }
}

.about__description-container h2 {
  font-size: 28px;
  line-height: 42px;
  font-weight: 600;
  width: 450px;
  margin: 0;
  margin-bottom: 23px;

  @media (max-width: $tablet-width) {
    width: 210px;
    font-size: 26px;
    line-height: 36px;
    margin-bottom: 16px;
    margin-top: -9px;
  }

  @media (max-width: $mobile-width) {
    font-size: 24px;
    line-height: 34px;
    width: 67%;
  }

  @media (max-width: $mobile-width) and (min-width: $adaptive-mobile) {
    width: 100%;
  }
}

.about__description-container p {
  font-size: 16px;
  line-height: 24px;
  margin: 0;

  @media (max-width: $tablet-width) {
    width: 324px;
  }

  @media (max-width: $mobile-width) {
    width: auto;
  }
}

.about__img-wrapper {
  width: auto;
  margin: 0;
  padding: 0;

  @media (max-width: $tablet-width) {
    width: auto;
    margin: 0;
    padding: 0;
  }

  @media (max-width: $mobile-width) {
    width: 90%;
  }
}

.about__description-container img {
  background-color: $color-light-blue;
  width: 560px;
  height: 301px;
  object-fit: cover;

  @media (max-width: $tablet-width) {
    width: 324px;
    height: 264px;
  }

  @media (max-width: $mobile-width) {
    width: 100%;
    height: auto;
  }
}
